(function() {
  'use strict';

  angular
    .module('coherentPaDemo')
    .controller('Claim8Controller', Claim8Controller);

  /** @ngInject */
  function Claim8Controller($state, $stateParams, ClaimService) {
    var vm = this;

    vm.information = {};

    vm.text1 = 'Confirm details';
    vm.text2 = 'Hospital Receipt';
    vm.text3 = 'Claim for';
    vm.text4 = ''
    vm.btn1 = 'Confirm';

    vm.proceed = function() {
      $state.go('claim-3');
    };

    function init() {
      if($stateParams.id) {
        if(ClaimService.claims[parseInt($stateParams.id, 10)]) {
          vm.information = ClaimService.claims[parseInt($stateParams.id, 10)];

          if(_.isEmpty(vm.information.files)) {
            $state.go('claim-1');
          }
        } else {
          $state.go('claim-1');
        }
      } else if(!_.isEmpty(ClaimService.claims)) {
        vm.information = ClaimService.claims[ClaimService.claims.length - 1];

        if(_.isEmpty(vm.information.files)) {
          $state.go('claim-1');
        }
      } else {
        $state.go('claim-1');
      }

      vm.startDate = moment(vm.information.hospitalizationStartDate).format('DD MMMM YYYY');
      vm.endDate = moment(vm.information.hospitalizationEndDate).format('DD MMMM YYYY');
    }

    init();
  }
})();

