(function() {
  'use strict';

  angular
    .module('coherentPaDemo')
    .controller('Claim6Controller', Claim6Controller);

  /** @ngInject */
  function Claim6Controller($state, $stateParams, ClaimService) {
    var vm = this;


    vm.a = ClaimService.ClaimBody

    vm.information = {};

    vm.text1 = 'Is the injured currently receiving treatment?';
    vm.btn1 = 'Yes';
    vm.btn2 = 'No';

    vm.text2 = 'Period of Hospitalization';
    vm.placeholder1 = ' /  / ';
    vm.placeholder3 = ' /  / ';

    vm.calendarFormat = 'yyyy / MM / dd';

    vm.maxHospitalDays = Math.round(ClaimService.policyDetails.Cont.Risks["1"].RiskBody.Benefit_Limits[2].PolicyLimit / 10000);

    vm.datepickerOptions1 = {
      showWeeks: false,
      minDate: moment(ClaimService.ClaimBody.Header.ClaimItems[0].ClaimEvent.EventStart).format('YYYY-MM-DD'),
      maxDate: moment()  
    };

    vm.datepickerOptions2 = {
      showWeeks: false,
      minDate: moment(vm.information.hospitalizationStartDate).format('YYYY-MM-DD'),
      maxDate: moment(vm.information.hospitalizationStartDate).add(vm.maxHospitalDays,'d').format('YYYY-MM-DD')  
    };

    vm.datepickerOptions3 = {
      showWeeks: false,
      minDate: vm.information.hospitalizationStartDate,
      maxDate: vm.information.hospitalizationEndDate 
    };

    vm.text3 = 'What treatment is the status of your treatment?';
    vm.treatments = [{
      display: 'Still in hospitalization',
      value: 'surgery 1'
    }, {
      display: 'Receiving outpatient treatment',
      value: 'surgery 2'
    }, {
      display: 'Treatment Completed',
      value: 'others'
    }];

    vm.text4 = 'What surgery did the injured get?';
    vm.placeholder4 = 'Choose surgery...';
    vm.surgeries = [{
      display: 'Surgery 1',
      value: 'surgery 1'
    }, {
      display: 'Surgery 2',
      value: 'surgery 2'
    }, {
      display: 'Others',
      value: 'others'
    }];

    vm.text5 = 'When was the surgery conducted?'
    vm.placeholder5 = ' /  / ';

    vm.btn3 = 'Continue';

    vm.setEndDateOptions = function () {
      vm.datepickerOptions2.minDate = moment(vm.information.hospitalizationStartDate).format('YYYY-MM-DD')
      vm.datepickerOptions2.maxDate = moment(vm.information.hospitalizationStartDate).add(vm.maxHospitalDays,'d').format('YYYY-MM-DD')
    }    

    vm.setSurgDateOptions = function () {
      vm.datepickerOptions3.minDate = moment(vm.information.hospitalizationStartDate).format('YYYY-MM-DD')
      vm.datepickerOptions3.maxDate = moment(vm.information.hospitalizationEndDate).format('YYYY-MM-DD')
      if (0 >= moment(vm.information.surgery).diff(moment(vm.information.hospitalizationEndDate))) {
        vm.information.surgery = vm.information.hospitalizationEndDate
      }
    }

    vm.proceed = function() {
      vm.a.Header.ClaimItems[0].ClaimEvent.EventStart = moment(vm.information.hospitalizationStartDate).format('YYYY-MM-DD')  + 'T13:23:45.871303Z'
      vm.a.Header.ClaimItems[0].ClaimEvent.EventEnd = moment(vm.information.hospitalizationEndDate).format('YYYY-MM-DD') + 'T13:23:45.871303Z'
      ClaimService.updateInformation(vm.information, $stateParams.id);
      ClaimService.setClaimBody(vm.a);

      $state.go('claim-7');
    };

    function init() {
      if($stateParams.id) {
        if(ClaimService.claims[parseInt($stateParams.id, 10)]) {
          vm.information = ClaimService.claims[parseInt($stateParams.id, 10)];
        } else {
          $state.go('claim-1');
        }
      } else if(!_.isEmpty(ClaimService.claims)) {
        vm.information = ClaimService.claims[ClaimService.claims.length - 1];
      } else {
        $state.go('claim-1');
      }
    }

    init();

  }
})();

