(function() {
  'use strict';

  angular
    .module('coherentPaDemo')
    .controller('Claim4Controller', Claim4Controller);

  /** @ngInject */
  function Claim4Controller($state, $stateParams, ClaimService) {
    var vm = this;

    vm.claimBodyTemplate = ClaimService.ClaimBody

    vm.benefit = '';

    vm.benefits = [
      {
        display: 'Death benefit',
        value: 'A1'
      },
      {
        display: 'Permanent disability insurance benefit',
        value: 'A2'
      },
      {
        display: 'Hospitalization insurance benefit',
        value: 'A3'
      },
      {
        display: 'Surgery insurance benefit (during hospitalization)',
        value: 'A4_a'
      },
      {
        display: 'Surgery insurance benefit (other)',
        value: 'A4_b'
      },
      {
        display: 'Personal Liability',
        value: 'B1'
      }
    ];

    vm.btn1 = 'Continue';

    vm.proceed = function() {
      ClaimService.setBenefit(_.find(vm.benefits, function(b)  {
        return b.value === vm.benefit;
      }), $stateParams.id);
      vm.claimBodyTemplate.Header.Contid = ClaimService.policyDetails.Cont.ContID;
      vm.claimBodyTemplate.Header.ClaimItems[0].Benefit = ClaimService.claims[0].benefit.value;
      ClaimService.setClaimBody(vm.claimBodyTemplate)

      $state.go('claim-5');
    };

    function init() {
      if($stateParams.id >= 0) {
        if(ClaimService.claims[parseInt($stateParams.id, 10)]) {
          vm.benefit = ClaimService.claims[parseInt($stateParams.id, 10)].benefit.value;
        } else {
          $state.go('claim-1');
        }
      } else if($stateParams.id !== '-1') {
        $state.go('claim-1');
      }
    }

    init();
  }
})();

